import { TgSubscription } from './telegram.model'

/**
 * Subscription MOCK
 */
export function TgSubscriptionMock(
  options?: Partial<TgSubscription>
): TgSubscription {
  const now = new Date().toISOString()

  return {
    _id: options?._id || 'TG_SUBSCRIPTION_ID',

    tgChatId: options?.tgChatId || 'TG_CHAT_ID',
    tgUserId: options?.tgUserId,
    type: options?.type,
    title: options?.title,
    username: options?.username || 'TG_USERNAME',
    userId: options?.userId,
    walletId: options?.walletId,
    isVerified: options?.isVerified,

    filters: options?.filters || {},
    options: options?.options,

    isActive: !!options?.isActive,

    createdAt: now,
    createdBy: options?.createdBy || 'USER_ID',
    updatedAt: now,
    updatedBy: options?.updatedBy || 'USER_ID',
  }
}
